.invisible{visibility:hidden}.msl-search-list-input{margin-bottom:10px}.msl-searchable-list{display:flex;flex-direction:column}.msl-searchable-list__items{border:1px solid #5b5d66;flex-basis:100%;overflow-y:auto;overflow-x:hidden}.msl-searchable-list__items--disabled{border:1px solid #d2d2d2}.msl-searchable-list__no-item{font-size:.8em;padding:5px 10px;color:#c0c2c6}.msl-searchable-list__item{font-size:.9em;padding:5px 10px;cursor:pointer;border-bottom:1px solid #252b45}.msl-searchable-list__item:hover{background:#3858e7;color:#fff}.msl-searchable-list__items--disabled .msl-searchable-list__item{border-bottom:1px solid #d2d2d2}.msl-searchable-list__item--disabled{cursor:default}.msl-searchable-list__item--disabled:hover{background:transparent;color:inherit}.msl-searchable-list__item--removed{background:red;color:#fff}.msl-searchable-list__item--added{background:green;color:#fff}.msl-multi-select{display:inline-flex;height:300px}.msl-multi-select__actions{display:flex;justify-content:center;flex-direction:column;align-items:center;padding:0 10px}.msl-multi-select__action{cursor:pointer}.msl-multi-select__action:hover{color:#c0c2c6}.msl-multi-select__action-select-all{margin-bottom:20px}.msl-multi-select__action-unselect-all{margin-top:20px}.msl-multi-select__action-icon{margin-top:17px;color:#919191}.msl-multi-select__list{width:100%}